import React from "react";
import { TextField, Button, Typography } from "@mui/material";
import { useState } from "react";
import axios from "axios";

function Form() {
  const [data, setdata] = useState({ slug: "", url: "" });
  const [success, setsuccess] = useState(false);
  const submit = () => {
    const result = axios.post("/url", data).then((res) => {
      if (res.data.slug && res.data.url) setsuccess({ slug: res.data.slug });
    });
    setdata({ slug: "", url: "" });
  };

  return (
    <div>
      <div>
        <TextField
          id="outlined-basic"
          label="slug"
          variant="outlined"
          value={data.slug}
          onChange={(e) => {
            setdata({ ...data, slug: e.target.value });
          }}
        />
      </div>
      <TextField
        id="outlined-basic"
        label="url"
        variant="outlined"
        value={data.url}
        onChange={(e) => {
          setdata({ ...data, url: e.target.value });
        }}
      />
      <div>
        <Button variant="contained" onClick={submit} onSubmit={submit}>
          Get shortened url
        </Button>
      </div>
      {success ? (
        <Typography>
          Url successfully shortened, please add {success.slug} after the
          current url to automatically visit the website
        </Typography>
      ) : null}
    </div>
  );
}

export default Form;
